<template>
  <div class="log-wrapper gg-flex-1 gg-flex-2">
    <div class="log-header gg-flex-3">
      <div class="log-header-search gg-flex-3">

        <div class="log-header-search-item gg-flex-3">
          <div class="log-header-search-item-title">systemName</div>
          <el-input v-model="ruleForm.systemName" placeholder=""></el-input>
        </div>
        <div class="log-header-search-item gg-flex-3">
          <div class="log-header-search-item-title">modelName</div>
          <el-input v-model="ruleForm.modelName" placeholder=""></el-input>
        </div>
        <div class="log-header-search-item gg-flex-3">
          <div class="log-header-search-item-title">userId</div>
          <el-input v-model="ruleForm.userId" placeholder=""></el-input>
        </div>
        <div class="log-header-search-item gg-flex-3">
          <div class="log-header-search-item-title">deviceInfo</div>
          <el-input v-model="ruleForm.deviceInfo" placeholder=""></el-input>
        </div>
        <div class="log-header-search-item gg-flex-3">
          <div class="log-header-search-item-title">keyWord1</div>
          <el-input v-model="ruleForm.keyWord1" placeholder=""></el-input>
        </div>
        <div class="log-header-search-item gg-flex-3">
          <div class="log-header-search-item-title">keyWord2</div>
          <el-input v-model="ruleForm.keyWord2" placeholder=""></el-input>
        </div>
        <div class="log-header-search-item gg-flex-3">
          <div class="log-header-search-item-title">keyWord3</div>
          <el-input v-model="ruleForm.keyWord3" placeholder=""></el-input>
        </div>
        <div class="log-header-search-item gg-flex-3">
          <div class="log-header-search-item-title">keyWord4</div>
          <el-input v-model="ruleForm.keyWord4" placeholder=""></el-input>
        </div>
        <div class="log-header-search-item gg-flex-3">
          <div class="log-header-search-item-title">message</div>
          <el-input v-model="ruleForm.message" placeholder=""></el-input>
        </div>
        <!-- <div class="log-header-search-item gg-flex-3">
          <div class="log-header-search-item-title">pageSize</div>
          <el-select v-model="ruleForm.pageSize" placeholder="">
            <el-option v-for="item in pageArr" :key="item" :label="item" :value="item">
            </el-option>
          </el-select>
        </div> -->

        <div class="log-header-search-item gg-flex-3">
          <div class="log-header-search-item-title"></div>
          <el-select v-model="timeLimit" placeholder="">
            <el-option v-for="item in timeList" :key="item.value" :label="item.label" :value="item.value">
            </el-option>
          </el-select>
        </div>
        <el-button class="log-header-search-Submit" type="primary" @click="onSubmit"></el-button>

      </div>
    </div>

    <div class="log-main">
      <el-table :data="tableList[pageIndex]" height="100%" border style="width: 100%">
        <!-- <el-table :data="tableList[pageIndex]" height="200" border style="width: 100%"> -->
        <el-table-column prop="systemName" label="systemName" width="180">
        </el-table-column>
        <el-table-column prop="modelName" label="modelName" width="180">
        </el-table-column>
        <el-table-column prop="userId" label="userId" width="180">
        </el-table-column>
        <el-table-column prop="deviceInfo" label="deviceInfo">
        </el-table-column>
        <el-table-column prop="keyWord1" label="keyWord1">
        </el-table-column>
        <el-table-column prop="keyword2" label="keyword2">
        </el-table-column>
        <el-table-column prop="keyword3" label="keyWord3">
        </el-table-column>
        <el-table-column prop="keyWord4" label="keyWord4">
        </el-table-column>
        <el-table-column prop="message" label="message">
        </el-table-column>
        <!-- <el-table-column prop="id" label="id" width="180">
        </el-table-column> -->
        <el-table-column prop="createTime" label="" width="180">
        </el-table-column>
        <el-table-column fixed="right" label="" width="100">
          <template slot-scope="scope">
            <el-button @click="handleClick(scope.row)" type="text" size="small"></el-button>
            <!--         <el-button type="text" size="small"></el-button> -->
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div class="log-footer gg-flex-1">
      <el-pagination ref="pagination" @size-change="handleSizeChange" :page-size='pageSize' @current-change="handleCurrentChange" :current-page="pageIndex" :page-sizes="pageArr" layout="total, sizes, prev, pager, next, jumper" :total="tableData.length">
      </el-pagination>
    </div>
  </div>
</template>

<script>
import qs from 'qs';       //qs
import axios from 'axios'
export default {
  name: "Log",
  components: {

  },
  data() {
    return {
      // ruleForm: {
      //   name: '',
      //   region: '',
      //   date1: '',
      //   date2: '',
      //   delivery: false,
      //   type: [],
      //   resource: '',
      //   desc: ''
      // },
      timeLimit: '1date',// 
      pageSize: 10,//
      pageIndex: 0,//
      ruleForm: {
        systemName: '',
        modelName: '',
        userId: '',
        deviceInfo: '',
        keyWord1: '',
        keyWord2: '',
        keyWord3: '',
        keyWord4: '',
        message: '',
        beginTime: null,
        endTime: null,
        // pageSize: 20,
        // pageIndex: 1,
      },
      pageArr: [10, 20, 50],
      timeList: [{
        value: '1date',
        label: '1'
      }, {
        value: '3date',
        label: '3'
      }, {
        value: '7date',
        label: '7'
      }, {
        value: '1Month',
        label: '1'
      }, {
        value: '3Month',
        label: ''
      }],
      tableList: [],
      tableData: [
        //  {"id":32018,"systemName":"","modelName":"","userId":"keid","deviceInfo":"abcd","keyWord1":"4","keyword2":null,"keyWord3":"lsdf","keyWord4":null,"message":"","createTime":1635495724012},{"id":32017,"systemName":"","modelName":"","userId":"12345","deviceInfo":"abcd","keyWord1":"4","keyword2":null,"keyWord3":"lsdf","keyWord4":null,"message":"","createTime":1635495690012},{"id":32016,"systemName":"","modelName":"","userId":"12345","deviceInfo":"abcd","keyWord1":"4","keyword2":null,"keyWord3":"lsdf","keyWord4":null,"message":"","createTime":1635495668735},{"id":32015,"systemName":"","modelName":"","userId":"12345","deviceInfo":"abcd","keyWord1":"4","keyword2":null,"keyWord3":"lsdf","keyWord4":null,"message":"","createTime":1635495640298}
      ],
    }
  },
  created() {
    // Array.prototype.in_array = function (e) {
    //   var r = new RegExp(',' + e + ',');
    //   return (r.test(',' + this.join(this.S) + ','));
    // }
    // 
    this.init();
  },
  mounted() {
    window.addEventListener('beforeunload', e => {
      this.beforeunloadFn(e);
    });
  },
  methods: {
    //  
    beforeunloadFn(e) {
      e = e || window.event;
      this.setLs('RefreshKey', 'true');
    },
    init() {
      //   
      Array.prototype.in_array = function (e) {
        var r = new RegExp(',' + e + ',');
        return (r.test(',' + this.join(this.S) + ','));
      } 
      //     
      if (this.getLs('RefreshKey') != '' && this.getLs('queryConditions')) {
        console.log(this.getLs('queryConditions'))
        this.ruleForm = this.getLs('queryConditions');
        this.pageSize = this.getLs('pageObj').pageSize;
        this.pageIndex = this.getLs('pageObj').pageIndex;
        this. timeLimit = this.getLs('pageObj').timeLimit;
        // 
        this.onSubmit();
      }
      this.ruleForm.beginTime=1627807635055;  this.ruleForm.endTime=1635756435055;
     
    }, 
    // 
    onSubmit() {
      // this.ruleForm.beginTime=1627802485480;  this.ruleForm.endTime=1635751285480;

      //  -  
      this.getTimeChange(this.timeLimit);
      //  - 
      this.setLs('queryConditions', JSON.stringify(this.ruleForm));
      //  - 
      this.setLs('pageObj', JSON.stringify({ pageSize: this.pageSize, pageIndex: this.pageIndex,timeLimit:this.timeLimit }));
       //  
      this.setLs('RefreshKey', '');

      // 
      console.log(this.ruleForm)
     
      let url= window.location.href;
      console.log( window.location)
      // if(url != 'http://log.woniu.com'){
      //   //  window.location.href = 'http://log.woniu.com';
      //     url == 'http://log.woniu.com';
      // } 
      process
      axios.get("/query", {
        params: this.ruleForm,
      }).then((res) => {
        console.log(res);
        if (res.status = 200) {
          let data = res.data.data.content;
          if (data.length >= 1) {
            console.log(data)
            for (let i in data) {
              data[i].createTime = this.getLocalTime(data[i].createTime);
            }
          }
          //  
          this.tableData = data;
          this.tableList = this.setTableList([], this.pageSize, this.tableData);
        }

        console.log(this.tableList)
        // this.tableData=res
      }).catch((res) => {
        console.log(res);
      })
      // axios({
      //   method: "POST",
      //   url: "/apis/users/queryUserInfo.wn",
      //   data: qs.stringify({ userId: '5bfcdc99aa55498aa1c5da6be672c136' }),
      //   headers: { 'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8', 'token': 'TGT-6900-pxvMMi3UCbyD4mc350bvUZ2skOf0wAhNBBfJTfqTPuEZTfujSi-oa.sso.woniu.com' }
      // }).then((res) => {
      //   console.log(res);

      // }).catch((res) => {
      //   console.log(res);
      // })
    },
    //  localStorage
    setLs(k, v) {
      var _v;
      if (typeof v === 'string' || typeof v === 'number') {
        _v = v;
      } else {
        _v = JSON.stringify(v);
      }
      try {
        localStorage.setItem(k, _v);
        return true;
      } catch (e) {
        console.log('');
        return false;
      }
    },
    //  localStorage
    getLs(k) {
      var str = localStorage.getItem(k);
      try {
        str = JSON.parse(str);
      } catch (e) {
      }
      return str === 'undefined' ? '' : str;
    },
    // 
    handleClick(row) {
      //   -params-JS + 
      console.log(row);
      this.$router.push({
        // name: "LogDetails",
        path: "/logDetails",
        query: {
          constraints: JSON.stringify(row),
        }
      });
    },
    // 
    handleSizeChange(val) {
      //  
      this.pageSize = val;
      console.log(this.pageSize)
      // 
      this.tableList = this.setTableList([], this.pageSize, this.tableData);
      console.log(` ${val} `);
    },
    // 
    handleCurrentChange(val) {
      //  
      this.pageIndex = (val -= 1);
      // console.log(this.pageIndex)
      // console.log(this.tableList[this.pageIndex])
      console.log(`: ${val}`);
    },
    // 
    setTableList(newArr, numb, arr) {
      newArr = [];
      let arrLength = arr.length; // 
      let num = numb;  //  10 
      let index = 0;
      for (let i = 0; i < arrLength; i++) {
        if (i % num === 0 && i !== 0) { //  10 
          newArr.push(arr.slice(index, i));
          index = i;
        };
        if ((i + 1) === arrLength) {
          newArr.push(arr.slice(index, (i + 1)));
        }
      };
      console.log(newArr)
      return newArr;
    },
    //   
    getLocalTime(nS) {
      return new Date(parseInt(nS)).toLocaleString().replace(/:\d{1,2}$/, ' ');
    },
    //    
    getTimeChange(selVal) {
      var nowTime = new Date();
      // 
      this.ruleForm.endTime = nowTime.getTime();
      switch (selVal) {
        case '1date':
          this.ruleForm.beginTime = this.getDate(nowTime, 1);
          break;
        case '3date':
          this.ruleForm.beginTime = this.getDate(nowTime, 3);
          break;
        case '7date':
          this.ruleForm.beginTime = this.getDate(nowTime, 7);
          break;
        case '1Month':
          this.ruleForm.beginTime = this.getTime(nowTime, 1);
          break;
        case '3Month':
          this.ruleForm.beginTime = this.getTime(nowTime, 3);
          break;
        default:
      };

      console.log(this.ruleForm.beginTime, this.getLocalTime(this.ruleForm.beginTime))
    },
    // index   (  24 * 60 * 60 * 1000)
    getDate(nowTime, index) {
      return nowTime.getTime() - index * 24 * 60 * 60 * 1000;
    },
    //     *  
    getTime(nowTime, index) {
      let begin = "";
      if (nowTime.getMonth() == 1) {//2
        if (nowTime.getFullYear() % 4 == 0) {//
          //  29
          begin = nowTime.getTime() - 29 * 24 * 3600 * 1000;
        } else {//
          //   28
          begin = nowTime.getTime() - 28 * 24 * 3600 * 1000;
        }
      } else {
        let month = new Array([2, 4, 6, 9, 11]);
        if (month.in_array(nowTime.getMonth())) {//
          //   30 
          begin = nowTime.getTime() - 30 * 24 * 3600 * 1000;
        } else {//
          //   31 
          begin = nowTime.getTime() - 31 * 24 * 3600 * 1000;
        }
      }
      index--;
      if (index == 0) {
        return begin;
      } else {
        return this.getTime(new Date(begin), index);
      };
    },
  },
  // 
  beforeDestroy() {
    console.group("beforeDestroy  Log===============");
  },
  destroyed() {
    alert(11)
    console.group("destroyed  Log===============");
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
@import "../../assets/css/log.css";
/* h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
} */
</style>



